import React, { Component } from "react";
import CharacterPics from "./components/CharacterPics";
import Wrapper from "./components/Wrapper";
import characters from "./characters.json";

class Game extends Component {

    state = {
        characters,
    };

    characterSelect = (id) => {
        console.log(id);
        var newArray = this.state.characters.map(character => {
            if (character.id === id) {
                character.selected = true;
            }
            return character
        })
        this.setState({
            characters: newArray
        })
        console.log(this.state.characters)
    };

    render() {
        return (
            <Wrapper>
                {this.state.characters.map(characters => (
                    <CharacterPics
                        characterSelect={this.characterSelect}
                        id={characters.id}
                        key={characters.id}
                        image={characters.image}
                        selected={characters.selected}
                    />
                ))}
            </Wrapper>
        );
    }
}

export default Game;
